.header{
    display: flex;
    border: 2px solid black;
    align-items: center;
    justify-content: space-between;
}
.logo{
    width: 150px;
}
.nav-items > ul{
    font-size: 20px;
    display: flex;
    gap: 1rem;
    list-style-type: none;
}
.nav-items{
    padding: 20px;
    margin: 10px;
}
.res-card{
    padding: 5px;
    width: 16.4%;
    border: 1px solid black ;
}
.res-card :hover{
    cursor: pointer;
    border: 1px solid black;
}
.search{
    padding: 20px;
}
.res-logo{
    height: 150px;
    width: 100%;
    
}
.res-container{
    display: flex;
    flex-wrap: wrap;
}